.header {
  overflow: auto;
  padding-bottom: 35px;

  @include onTablet {
    padding-bottom: 44px;
  }

  @include onDesktop {
    padding-bottom: 100px;
  }

  &__bg {
    background-image: url("../images/header/header-bg.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    position: absolute;
    right: 0;
    top: 0;
    height: 300px;
    width: 320px;

    @include onTablet {
      width: 374px;
      height: 384px;
    }

    @media (min-width: 850px) {
      width: 725px;
      height: 655px;
      background-image: url("../images/header/header-bg-2x.jpg");
    }
  }

  &__grid-wrapper {
    @include pageGrid;

    grid-template-rows: 79px 220px 134px 50px;
    position: relative;
    z-index: 1;
    align-items: center;

    @include onTablet {
      grid-template-rows: 102px 180px 89px 13px;
    }

    @media (min-width: 850px) {
      grid-template-rows: 102px 58px 262px 160px 73px;
    }
  }

  &__icons-burger-link {
    display: block;
    position: relative;
    width: 24px;
    height: 20px;

    &--border-top,
    &--border-mid,
    &--border-bot {
      height: 4px;
      border-radius: 5px;
      background-color: black;
      width: 100%;
      transition: width $effectDuration $effectFunc 0s,
        background-color $effectDuration $effectFunc 0s;

      position: absolute;
      left: 0;
    }

    &--border-top {
      top: 0;
    }

    &--border-mid {
      top: 50%;
      transform: translateY(-50%);

      @include onDesktop {
        transform: translateY(-51%);
      }
    }

    &--border-bot {
      bottom: 0;
    }

    &:hover &--border-top {
      width: 85%;
      background-color: black;
    }

    &:hover &--border-bot {
      width: 60%;
    }
  }

  &__logo {
    transform: translateX(-68%);

    @include onTablet {
      grid-column: 5 / 6;
      grid-row: 1 / 2;
    }

    @include onDesktop {
      grid-column: 8 / 9;
    }
  }

  &__title {
    grid-column: 1 / -1;
    grid-row: 3 / 4;
    max-width: 280px;

    @media (min-width: 321px) {
      & {
        justify-self: center;
      }
    }

    @include onTablet {
      grid-column: 1 / 4;
      grid-row: 2 / 3;
      justify-self: start;
      text-align: left;
      max-width: 100%;
    }

    @media (min-width: 850px) {
      grid-column: 1 / 4;
      grid-row: 3 / 4;
      max-width: 470px;
    }

  }

  &__button {
    grid-column: 1 / 3;
    grid-row: 4 / 5;
    max-width: 280px;

    @media (min-width: 430px) {
      & {
        justify-self: center;
      }
    }

    @include onTablet {
      grid-column: 1 / 4;
      grid-row: 3 / 4;
      justify-self: start;
      max-width: 200px;
      min-height: 70px;
    }

    @media (min-width: 850px) {
      grid-column: 1 / 3;
      grid-row: 4 / 5;
    }

    @include onDesktop {
      grid-column: 3 / 5;
      width: 270px;
    }
  }

  &__decor {
    display: none;
    color: $grey;
    font-weight: 700;
    text-transform: uppercase;

    position: absolute;
    left: -48px;
    bottom: 62px;
    padding-left: 82px;

    transform: rotate(-90deg) translate(0,-50%);

    &::after {
      content: "";
      background-color: $green;
      position: absolute;
      height: 1px;
      width: 70px;
      left: -8px;
      bottom: 7px;
    }

    @include onDesktop {
      display: block;
    }
  }

}
